@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
link {
  margin: 0;
  padding: 0;
  border: 0;
  color: rgb(71, 71, 71);
}

.App {
  text-align: center;
}
body{
  -ms-overflow-style: none;
  }
  
::-webkit-scrollbar {
  display: none;
}

/* --- Header ---*/
.head-container {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;  
  margin-top: 2em;
  margin-bottom: 1.1em;
  margin-left: 10%;
  margin-right: 10%;
}

.header-logo {
  width: 30%;
  font-size: 1.4rem;
  margin: auto;
  margin-left: 0;
}

.header-menu {
  width: 40%;
  display: flex;
  flex-flow: row wrap;
  font-size: 1.2rem;
  align-items: center;
  text-align: center;
  justify-content: space-evenly;
}

.header-items {
  flex-grow: 1;
}

.header-items:hover {
  color: darkcyan;
  cursor: pointer;
}

.header-sns {
  display: flex;
  width: 5%;
  margin-left: 50px;
  align-items: center;
  text-align: center;
}

.icon {
  margin-right: 15px;
}

.header-burger {
  margin-top: auto;
  margin-bottom: auto;
}

@media screen and (min-width: 900px) {
.header-burger {
  display: none;
}
}

@media screen and (max-width: 900px) {
.header-menu {
  display: none;
}

.header-sns {
  display: none;
}
}

/* --- Bg ---*/
.bg {
  height: 1fr;
}

.sliderimg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 900px) {
  .sliderimg{
    width: auto;
    height: 100%;
  }
}


/* -- bottom menu -- */
.detail-menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: auto;
  font-size: 1.1rem;
  justify-content: space-evenly;
}


/* -- page -- */
.contents-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10vh auto 15vh auto;
}

.contents {
  margin: 30px;
  justify-content: center;
  border-radius: 10px;
}

.box {
  background-color: rgb(245, 245, 245);
  width: 540px;
  padding: 30px;
}

.img {
  min-width: 300px;
  width: 600px;
  object-fit: cover;
  height: auto;
}

h3 {
  margin-bottom: 20px;
}

p{
  margin-top: 10px;

}

.bt {
  background-color: #c1c1c1 ;
  color: white;
  font-size: 1.1rem;
  border: 0px;
  border-radius: 10px;
  width: 100px;
  height: 40px;
  margin-top: 20px;
}

/* -- Weather -- */

.weather {
  flex-direction: column;
  align-items: center;
}

.styleCard {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 50px;
  justify-content: center;
  gap: 1rem;
}


.card {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 20px;
  width: 140px;
  height: 140px;
  border-radius: 20px;
  background-color: #c1c1c1;
  text-align: center;
  justify-items: center;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

.card-header {
  background-color: #1fc4ad;
}

.styleCardTitle {
  font-size: 27px;
  font-weight: bold;
  text-shadow: 0 3px 6px rgb(0 0 0 / 16%);
  color: #fff;
  padding: 5px
};

.styleCardContent {
  font-size: 16px;
  text-align: center;
  font-weight: bold;
  text-shadow: 0 3px 6px rgb(0 0 0 / 16%);
  color: #fff;
};




@media (max-width: 600px) {
  .styleCard {
    grid-template-columns: repeat(1, 180px);
  }
}

